@charset "utf-8";
/* 공통 이펙트 _ 페이드인업 */
.trnasition_fadeInUp {transform: translate3d(0, 100%, 0); opacity: 0;}
.swiper-slide-active .trnasition_fadeInUp {transition: all 1s ease-in-out; transform: translate(0); opacity: 1; }
.swiper-slide-active .trnasition_delay_5 {transition-delay: .5s;}
.swiper-slide-active .trnasition_delay_10 {transition-delay: 1s;}
.swiper-slide-active .trnasition_delay_15 {transition-delay: 1.5s;}
.swiper-slide-active .trnasition_delay_20 {transition-delay: 2s;}
.swiper-slide-active .trnasition_delay_25 {transition-delay: 2.5s;}
.swiper-slide-active .trnasition_delay_30 {transition-delay: 3s;}

#root, .App, .swiper {height:100%;}
.swiper-slide {position: relative; display: flex; align-items: center;  flex-wrap:wrap; color:#333; background-position: center; background-repeat: no-repeat; background-size: cover; overflow: hidden;}
.swiper_inner {position:relative; width:1140px; margin:0 auto; z-index: 10;}


/* page_bg */
.slide1 {background-image: url("//img.enuri.info/images/summerce/bg_nav01.png")}
.slide2 {background-image: url("//img.enuri.info/images/summerce/bg_nav02.jpg");}
.slide3 {background-image: url("//img.enuri.info/images/summerce/bg_nav03.jpg");}
.slide4 {background-image: url("//img.enuri.info/images/summerce/bg_nav06.jpg");}
.slide5 {background-image: url("//img.enuri.info/images/summerce/bg_nav06.jpg");}
.slide6 {background-image: url("//img.enuri.info/images/summerce/bg_nav04.jpg");}
.slide7 {background-image: url("//img.enuri.info/images/summerce/bg_nav05.jpg");}

/* slide1_ intro */
.slide1 .txt_1{font-size:36px; font-family: 'Roboto'; font-weight:100; line-height: 1;}
.slide1 .txt_2{font-size:100px; color:#25b1e6; letter-spacing:-3px; font-family: 'Roboto'; font-weight:700; margin-top:-8px; line-height: 1;}
.slide1 .txt_3{font-size:100px; letter-spacing:4px; font-family: 'Roboto'; font-weight:100; margin-top:-15px; line-height: 1;}
.slide1 .txt_4{font-size: 20px; line-height: 1.3; padding-top: 27px; position: relative; margin-top: 25px;}
.slide1 .txt_4 span{position:absolute; left:6px; top:0; width:100px; height:3px; background:#1e96c7}
.slide1 .obj{position:absolute; right:100px; top: 50%; -webkit-transform: translateY(-45%); transform: translateY(-45%); background:url("//img.enuri.info/images/summerce/obj_nav01.png"); width:921px; height:816px;}

/* slide2_ about us */
.slide2 .swiper_outter {position:absolute; bottom:0; right:20%; width:401px;}
.slide2 .about_tit {font-size:76px; color:#21a0d4; text-align:center; letter-spacing:-3px; line-height: 1;}
.slide2 .about_tit .txt_1 {font-weight:100; margin-bottom:-10px}
.slide2 .about_tit .txt_2 {font-weight:700;}
.slide2 .about_body {background:url("//img.enuri.info/images/summerce/obj2_nav02.png") no-repeat 0 0; color:#fff; padding:136px 20px 87px; margin-top: 36px;}
.slide2 .about_body .txt_1{font-size:36px; letter-spacing:-4px; line-height:1.2}
.slide2 .about_body .txt_1 span{font-size:46px;}
.slide2 .about_body .txt_2{ font-size:16px; line-height:1.6; letter-spacing:-1px; margin-top:24px;}

/* slide3_ business */
.slide3 .fixedtit {margin-bottom: -130px;}
.slide3 .fixedtit .txt_1 {color: #fff; font-size: 76px; font-weight: 700; letter-spacing: -3px;}
.slide3 .fixedtit .txt_2 {color: #fff; font-size: 20px; letter-spacing: -2px; line-height: 24px;}
.slide3 .businessSwiper {position:relative;}
.slide3 .businessslide {width: 100%; height:547px;}
.slide3 .business_1 {background:url("//img.enuri.info/images/summerce/com_enuri_v1.png") no-repeat center;}
.slide3 .business_2 {background:url("//img.enuri.info/images/summerce/com_shallwead_v1.png") no-repeat center;}
.slide3 .business_3 {background:url("//img.enuri.info/images/summerce/com_sweet_v1.png") no-repeat center;}
.slide3 .business_4 {background:url("//img.enuri.info/images/summerce/com_bigdata_v1.png") no-repeat center;}
.slide3 .business_5 {background:url("//img.enuri.info/images/summerce/com_builton_v1.png") no-repeat center;}

/* slide3_ business _ custom navigationi */
.slide3 .btn_slide_custom {position: absolute; top: 50%; width: 48px; height: 93px; margin-top: -22px; z-index: 10; cursor: pointer; background-repeat: no-repeat; background-image: url(http://img.enuri.info/images/summerce/ico.png); background-color: transparent; border: none;}
.slide3 .btn_slide_prev {left:-80px; background-position: -1px -57px;}
.slide3 .btn_slide_next {right:-80px; background-position: -96px -57px;}
.slide3 .swiper-button-disabled {opacity: .5; }

/* slide3_ business _ custom pagination */
.slide3 .swiper-pagination {text-align: center; font-size: 0; margin-top:10px}
.slide3 .swiper-pagination .swiper-pagination-bullet {display: inline-block; width:16px; height:16px; margin: 0 5px; background-color: #fff; border-radius: 50%; opacity: .2;}
.slide3 .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {background-color: #000; opacity: .5;}

/* slide4,5_ board _ news,notice */
.board_name {color: #21a0d4; font-size: 76px; font-weight: 700; border-bottom: 3px solid #21a0d4; width: 100%; margin-bottom: 20px;}
.board_list {display: flex; flex-wrap: wrap; margin:0 -10px; }

.board_list li {flex:0 0 auto; width:33.333333%; padding:0 10px; transform: translate3d(0, 100%, 0); opacity: 0;}
.board_list li a {position: relative; display: block; padding: 18px; color: #555555; overflow: hidden; margin-bottom: 20px; border: 1px solid #cccccc; text-decoration: none;}
.board_list li a .board_date {font-size: 14px; color: #787878; margin-bottom: 4px;}
.board_list li a .board_subject {font-size: 18px; font-weight: bold; color: #171717; display: block; margin-bottom: 7px; text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical; word-wrap: break-word; line-height: 1.2em; height: 2.4em; overflow: hidden;}
.board_list li a .board_content {max-height: 140px; overflow: hidden; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical; word-wrap: break-word;}
.board_list li a:hover {border-color: #000; box-shadow: 0 0 8px 2px rgba(0,0,0,.25);}

.swiper-slide-active .board_list li {transition: all 1s ease-in-out; transform: translate(0); opacity: 1; }
.swiper-slide-active .board_list li:nth-child(1) {transition-delay: .3s;}
.swiper-slide-active .board_list li:nth-child(2) {transition-delay: .6s;}
.swiper-slide-active .board_list li:nth-child(3) {transition-delay: .9s;}
.swiper-slide-active .board_list li:nth-child(4) {transition-delay: 1.2s;}
.swiper-slide-active .board_list li:nth-child(5) {transition-delay: 1.5s;}
.swiper-slide-active .board_list li:nth-child(6) {transition-delay: 1.8s;}
.swiper-slide-active .board_list li:nth-child(7) {transition-delay: 2.1s;}
.swiper-slide-active .board_list li:nth-child(8) {transition-delay: 2.4s;}
.swiper-slide-active .board_list li:nth-child(9) {transition-delay: 2.7s;}

/* boardlist pagination */
.pagination {font-size: 0; text-align: center;}
.pagination a {display: inline-block; vertical-align: middle; line-height: 38px; padding:0 6px; font-size: 15px; color:#333; border:1px solid #eee; min-width: 40px; border-radius: 4px; margin:0 4px;}
.pagination a.active {color:#fff; background-color: var(--color-blue-6); border-color:var(--color-blue-6);}

/* boardlist all view */
.board_allview {width:1140px; padding-top:80px; margin:0 auto;}
.board_allview .board_name {position:relative;}
.board_allview .board_sort_listCnt {position:absolute; bottom:20px; right:0; line-height: auto; cursor: pointer; z-index: 10; margin-top:45px;}
.board_allview .board_sort_listCnt .selectedCnt {font-size: 14px; width: 110px; font-weight: normal; border:1px solid #999; padding:4px 0; color:#666; border-radius: 4px; background-color: #fff; text-align: center;}
.board_allview .board_sort_listCnt ul {position:absolute; display: none; top:100%; left:0; width:100%; border:1px solid #999; margin-top:-1px; border-radius: 4px; overflow: hidden;}
.board_allview .board_sort_listCnt ul li {}
.board_allview .board_sort_listCnt ul li .btn_sort_listCnt {display: block; width: 100%; padding:4px 0; border:none; background: none; text-align: center; background-color: #fff; font-size: 14px;}
.board_allview .board_list li {transform: translate(0); opacity: 1;}

.board_allview .board_sort_listCnt:hover ul {display: block;}
.board_allview .board_sort_listCnt ul li .btn_sort_listCnt:hover {background-color: #f9f9f9;}

/* boardView */
.boardView {overflow: auto; height: 100%;}
.boardView .boardView_detail {width:1140px; margin:0 auto; padding-top:100px;}
.boardView .boardView_detail .subject{position: relative; font-size: 24px; font-weight: bold; color: #171717;  border-bottom: 1px solid #e2e2e2; margin-bottom: 60px; padding: 0 100px 10px 0;}
.boardView .boardView_detail .date{position:absolute; top:4px; right:0; color: #787878; font-size: 14px; font-weight: normal; }
.boardView .boardView_detail .content{font-size: 16px; line-height: 28px;}
.boardView .btn_to_list {display: block; width:100px; line-height: 38px; border:1px solid #ddd; text-align: center; margin:30px auto; border-radius: 4px; color:#444; text-decoration: none; font-size: 14px;}

.boardView.boardNews .content {overflow: hidden; display: -webkit-box; -webkit-line-clamp: 4; -webkit-box-orient: vertical; word-wrap: break-word;}

/* board _ navigation */
.board_navigation {width:1140px; margin:30px auto 150px; border-top: 1px solid #ddd;}
.board_navigation a {position: relative; display: block; border-bottom: 1px solid #e2e2e2; font-size: 16px; line-height: 48px; padding:0 100px;}
.board_navigation a > span {display: inline-block; vertical-align: middle; text-align: center;}
.board_navigation a .navi_prefix {position:absolute; top:1px; left:0; width: 100px; color: #787878; font-size: 14px; }
.board_navigation a .navi_date {position:absolute; top:1px; right:0; width: 100px; color:#888; font-size: 14px;}
.board_navigation a .navi_title {color:#444; white-space: nowrap; text-overflow: ellipsis; overflow: hidden; word-break: break-all; text-align: left;}

/* scrollbar */
::-webkit-scrollbar { width: 4px; height: 4px;}
::-webkit-scrollbar-track { background-color: #f2f2f2; -webkit-border-radius: 2px; border-radius: 2px; }
::-webkit-scrollbar-thumb { background: #30a7f7; -webkit-border-radius: 2px; border-radius: 2px; }
::-webkit-scrollbar-button { display: none; }

