.emptyPage {display: flex; align-items: center; justify-content: center; width: 100%; height: 100%;}
.emptyPage .emptyPage_inner_wrap {text-align: center;}
.emptyPage .emptyPage_inner_wrap a {display: block; width:100px; line-height: 38px; border:1px solid #ddd; text-align: center; margin:20px auto; border-radius: 4px; color:#444; text-decoration: none; font-size: 14px;}